@import '../../../scss/variables.scss';

.categoriesList {
  display: flex;
  align-items: center;
  column-gap: 10px;

  list-style: none;
}

.categoriesList__item {
  padding: 14px 15px;

  font-size: 16px;
  color: #fff;

  background-color: $color_light;
  border: none;
  border-radius: 30px; 

  cursor: pointer;

  @media (hover: hover) {
    &:hover {
      background-color: lighten($color_basic, 10%);
    }
  }

  &.current {
    background-color: $color_accent;
  }
}